import { useState } from "react";
import ReactDom from "react-dom";
import "./styles.css";

export default function App() {
  const [estado, setEstado] = useState("entrada");

  const [palpite, setPalpite] = useState(150);
  const [numPalpites, setNumPalpites] = useState(1);

  const [min, setMin] = useState(0);
  const [max, setMax] = useState(300);

  const iniciarJogo = () => {
    setEstado("Rodando");
    setMin(0);
    setMax(300);
    setNumPalpites(1);
    setPalpite(150);
  };

  if (estado === "entrada") {
    return <button onClick={iniciarJogo}>Começar Jogo! </button>;
  }
  const menor = () => {
    setNumPalpites(numPalpites + 1);
    setMax(300);
    const proxPalpite = parseInt((palpite - min) / 2) + min;
    setPalpite(proxPalpite);
  };

  const maior = () => {
    setNumPalpites(numPalpites + 1);
    setMin(palpite);
    const proxPalpite = parseInt((max - palpite) / 2) + palpite;
    setPalpite(proxPalpite);
  };

  const acertou = () => {
    setEstado("Fim");
  };

  if (estado === "Fim") {
    return (
      <div>
        <p>
          Acertei o numero {palpite} com {numPalpites} chutes
        </p>
        <button onClick={iniciarJogo}>Reinicie o Jogo</button>
      </div>
    );
  }
  return (
    <div className="App">
      <p>
        O objetivo e entender sobre useState ele armazena fora do escopo uma
        informação como uma foto e ultiliza quando for solicitado{" "}
      </p>
      <p>o seu numero e {palpite}</p>
      <button onClick={acertou}>Acertou</button>
      <button onClick={menor}>Menor</button>
      <button onClick={maior}>Maior</button>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDom.render(<app />, rootElement);
